import {controller, target, targets} from '@github/catalyst'

@controller
export class MoveWorkContainerElement extends HTMLElement {
  MAX_SELECTION_COUNT = 50
  RESOURCE_TYPES = {
    repository: 'repository',
    project: 'project',
  }

  @target moveResourcesCTA: HTMLElement
  @target destinationOrgForm: HTMLFormElement
  @target maxSelectionNotice: HTMLElement
  @target destinationOrgInput: HTMLInputElement

  @targets resourceItems: NodeListOf<HTMLInputElement>
  @targets selectAllTriggers: NodeListOf<HTMLElement>

  toggleMoveWorkCTA = (isEnabled: boolean) => {
    if (!this.moveResourcesCTA) return

    if (isEnabled) {
      this.moveResourcesCTA.removeAttribute('disabled')
      this.moveResourcesCTA.style.pointerEvents = 'all'
    } else {
      this.moveResourcesCTA.setAttribute('disabled', 'disabled')
      this.moveResourcesCTA.style.pointerEvents = 'none'
    }
  }

  toggleSelectionList = (resourceType: string, checkList: HTMLInputElement[]) => {
    const selectTrigger = Array.from(this.selectAllTriggers).find(
      trigger => trigger.getAttribute('data-resource-type') === resourceType,
    )!

    selectTrigger.classList.toggle('selected')
    selectTrigger.textContent = selectTrigger.classList.contains('selected') ? 'Unselect all' : 'Select all'
    for (const checkbox of checkList) {
      checkbox.checked = !selectTrigger.classList.contains('selected')
      checkbox.click()
    }
  }

  chooseDestinationOrg = (event: PointerEvent) => {
    const triggerElement = event.currentTarget as HTMLElement
    const selectedOrgId = triggerElement.getAttribute('data-org') as string
    this.destinationOrgInput.value = selectedOrgId
    this.destinationOrgForm.submit()
  }

  toggleMaxSelectionNotice = (noticeType?: string) => {
    if (typeof noticeType === 'undefined') {
      this.maxSelectionNotice.setAttribute('hidden', 'true')
    } else {
      this.maxSelectionNotice.textContent = this.maxSelectionNotice.getAttribute(noticeType)
      this.maxSelectionNotice.removeAttribute('hidden')
    }
  }

  loadMoreResources = (event: PointerEvent) => {
    const loadMoreCTA = event.currentTarget as HTMLElement
    const parent = loadMoreCTA.closest('.js-show-more-resources')!
    const fragment = parent.querySelector<HTMLElement>('.js-show-more-resources-fragment')!
    fragment.hidden = false
    loadMoreCTA.hidden = true
  }

  selectResources = (event: PointerEvent) => {
    const triggerElement = event.currentTarget as HTMLElement
    const selectedResourceType = triggerElement.getAttribute('data-resource-type') as string

    const selection = Array.from(this.resourceItems).filter(
      resource => resource.getAttribute('data-resource-type') === selectedResourceType,
    )
    this.toggleSelectionList(selectedResourceType, selection)
  }

  selectResourceItem = () => {
    const selectedResources = Array.from(this.resourceItems).filter(checkbox => checkbox.checked)
    const selectedRepos = selectedResources.filter(
      checkbox => checkbox.getAttribute('data-resource-type') === this.RESOURCE_TYPES.repository,
    )
    const selectedProjects = selectedResources.filter(
      checkbox => checkbox.getAttribute('data-resource-type') === this.RESOURCE_TYPES.project,
    )

    if (!selectedResources.length) {
      this.toggleMoveWorkCTA(false)
      this.toggleMaxSelectionNotice()
    } else {
      this.toggleMoveWorkCTA(true)
      this.toggleMaxSelectionNotice()

      if (selectedRepos.length > this.MAX_SELECTION_COUNT) {
        this.toggleMoveWorkCTA(false)
        this.toggleMaxSelectionNotice('data-notice-repository')
      }

      if (selectedProjects.length > this.MAX_SELECTION_COUNT) {
        this.toggleMoveWorkCTA(false)
        this.toggleMaxSelectionNotice('data-notice-project')
      }
      if (selectedRepos.length > this.MAX_SELECTION_COUNT && selectedProjects.length > this.MAX_SELECTION_COUNT) {
        this.toggleMoveWorkCTA(false)
        this.toggleMaxSelectionNotice('data-notice-all')
      }
    }
  }
}
